  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
}
.header__logo {
  width: 258px;
  height: 207px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header__logo img {
  width: 258px;
  height: 207px;
  object-fit: contain;
  display: block;
  pointer-events: auto;
  z-index: 1;
}
.header__nav-login {
  position: static;
  margin-top: 1rem;
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.header__nav-login:hover {
  color: #007df4;
  cursor: pointer;
}
.header__nav-login--actual {
  color: #007df4;
}
@media (min-width: 768px) {
  .header__nav-login {
    white-space: nowrap;
    position: absolute;
    right: 2rem;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 0;
  }
}

.mobile-menu {
  display: flex;
  justify-content: center;
  background-color: rgb(53.625, 55.6875, 43.3125);
  cursor: pointer;
}
