  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.navegacion__contenedor {
  height: 0;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  visibility: hidden;
  background-color: rgb(53.625, 55.6875, 43.3125);
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.navegacion__contenedor--mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.navegacion__contenedor :nth-child(odd) {
  background-color: rgb(67.4375, 70.03125, 54.46875);
  width: 100%;
  padding: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion__contenedor :nth-child(odd) {
    width: auto;
  }
}
@media (min-width: 768px) {
  .navegacion__contenedor {
    visibility: visible;
    opacity: 1;
    height: auto;
    position: relative;
    z-index: 2;
    background-color: rgb(53.625, 55.6875, 43.3125);
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 25rem;
    padding: 0.5rem;
  }
  .navegacion__contenedor :nth-child(odd) {
    background-color: transparent;
  }
}
.navegacion__enlace {
  font-size: 1.8rem;
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.navegacion__enlace:hover {
  color: #007df4;
  cursor: pointer;
}
.navegacion__enlace--actual {
  color: #007df4;
}

.footer {
  background-color: #1a1b15;
  margin-top: 5rem;
  padding-top: 0.4rem;
  color: #FFFFFF;
}
.footer__copyright {
  background-color: rgb(53.625, 55.6875, 43.3125);
  margin: 2rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
}
.footer__copyright--regular {
  font-weight: 400;
}

.nosotros__grid {
