  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.nosotros__grid img {
  width: 90%;
  height: auto;
  margin: 0 auto 2rem auto;
  display: block;
}
.nosotros__texto {
  line-height: 2;
}

.comprar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.comprar__titulo {
  color: #1a1b15;
