}
.comprar__bloques {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  width: 75%;
}
@media (min-width: 768px) {
  .comprar__bloques {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
