.comprar__bloque {
  border: 1px solid rgb(127.050209205, 141.7782426778, 162.949790795);
  padding: 3rem;
  text-align: center;
}
.comprar__bloque__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.comprar img {
  width: 100px;
  height: auto;
