.menu-social {
  display: block;
  justify-items: center;
  margin: 2rem 0 0 0;
}
.menu-social__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-social__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu-social__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-social__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-social__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-social__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-social__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-social__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-social {
    display: flex;
