#splash-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden;
}

.splash-logo-container {
  position: relative;
}

#splash-logo {
  width: 100%;
  height: 250px;
  opacity: 0;
  transform: scale(0.5);
  animation: logoPulse 2s forwards;
  z-index: 2;
}

#tsparticles {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  pointer-events: none;
  transform: translate(-50%, -50%);
  z-index: 1;
}

@keyframes logoPulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  40% {
    transform: scale(1.3);
    opacity: 1;
  }
  60% {
    transform: scale(0.9);
  }
  80% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
#splash-screen.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

