@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

    &--centrar {
        justify-content: center;
    }

    &__enlace {
        color: v.$negro;
        font-size: 1.4rem;
        font-weight: v.$delgada;

        &--confirmado {
            @include m.boton(v.$azul, v.$blanco);
            width: auto;
        }
    }

    
}